.todayExpenseLi {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 30vh;
    padding: 0;
    list-style: none;
    /* width: 35rem; */
    overflow: auto;
}

.dayExpLi {
    display: flex;
    justify-content: space-between;
    margin: 3px;
    /* max-width: 20vw; */
    box-shadow: 1px 1px #9E354A;
}

.padding0 {
    padding: 0;
}

.box1 {
    padding: 5px;
    margin-top: 5px;
    border: 2px #2C8437 solid;
    border-radius: 15px;
    /* box-shadow: 1px 1px rgba(146, 76, 184, 05); */
}

.expenseDate {
    font-size: 1.1em;
    font-weight: 400;
}

.marginB5 {
    margin-bottom: 8px;
}

.itemBox {
    margin: 6px!important;
    padding: 8px;
    border: 1px black solid;
    width: max-content;
    border-radius: 10px;
    box-shadow: 1px 1px;
}

.borderBottom1 {
    border-bottom: 1px black solid;
    padding-bottom: 4px;
}