.ExpenseBar {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    max-width: 38em;
}

.exBarContainer {
    margin-bottom: 10px;
}

.exBarUl {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 5em;
    padding: 0;
    list-style: none;
    overflow-x: auto;
}

.deleteItem {
    margin-left: 10px;
}

.multipleItems {
    display: flex;
    align-items: center;
}

button {
    border: none;
}

button:hover {
    cursor: pointer;
}

.singleItems {
    border-radius: 2px;
    font-size: 13px;
    margin-bottom: 5px;
}

.multipleItems {
    border-radius: 2px;
    margin-top: 3px;
    background-color: #709DEF;
    color: white;
    margin-bottom: 5px;
}